import React, { Component } from 'react';
import {Dropdown, Menu} from 'semantic-ui-react';

export default class Header extends Component {

    state = {
        languageOptions: [
            { key: 'Turkish', text: 'Türkçe', value: 'tr' },
            { key: 'English', text: 'English', value: 'en' }
        ]
    };

    handleItemClick = (e, { value } ) => {
        this.changePage(value)
    };

    changePage(name){
        window.location.href = "/#/" + name;
        this.setState({activeItem: name});
    }

    handleLangChange = (e, { value }) => {
        const { activeItem } = this.state;
        this.setState({lang: value});

        switch (value) {
            case 'tr':
                this.changePage(activeItem + "_tr");
                break;
            case 'en':
                this.changePage(activeItem.slice(0, -3));
                break;
            default:
                this.changePage(activeItem);
        }
    };

    componentDidMount()
    {
        this.setState({
            activeItem: /[^/]*$/.exec(window.location.href)[0],
            lang: (window.location.href.substr(window.location.href.length - 2) === 'tr' ? 'tr' : 'en')
        });
    }

    componentDidUpdate() {
        const { activeItem, lang } = this.state;

        if(
            activeItem !== /[^/]*$/.exec(window.location.href)[0]
            || lang !== (window.location.href.substr(window.location.href.length - 2) === 'tr' ? 'tr' : 'en')
        ) {
            this.setState({
                activeItem: /[^/]*$/.exec(window.location.href)[0],
                lang: (window.location.href.substr(window.location.href.length - 2) === 'tr' ? 'tr' : 'en')
            });
        }
    }

    render() {

        const { activeItem, lang, languageOptions } = this.state;

        if(activeItem === ''){
            this.setState({
                activeItem: 'main_page_tr',
                lang: 'tr'
            });
            window.location.href = "/#/main_page_tr";
        }

        return (
            <Menu color="blue" widths={4} size="massive" classname="min-width-480" borderless>
                <Menu.Item
                    name={ lang === 'en' ? 'main_page' : 'ana_sayfa' }
                    value={ lang === 'en' ? 'main_page' : 'main_page_tr' }
                    active={activeItem === ( lang === 'en' ? 'main_page' : 'main_page_tr') }
                    onClick={this.handleItemClick}
                    className = "c9-text-big"
                />

                <Menu.Item
                    name={ lang === 'en' ? 'trial_membership' : 'deneme_üyeliği' }
                    value={ lang === 'en' ? 'trial_membership' : 'trial_membership_tr' }
                    active={activeItem === ( lang === 'en' ? 'trial_membership' : 'trial_membership_tr') }
                    onClick={this.handleItemClick}
                    className = "c9-text-big"
                />

                <Menu.Item
                    name={ lang === 'en' ? 'paid_membership' : 'ücretli_üyelik' }
                    value={ lang === 'en' ? 'paid_membership' : 'paid_membership_tr' }
                    active={activeItem === ( lang === 'en' ? 'paid_membership' : 'paid_membership_tr') }
                    onClick={this.handleItemClick}
                    className = "c9-text-big"
                />

                <Menu.Item>
                    <Dropdown
                        button
                        className='icon c9-text-big'
                        floating
                        labeled
                        icon='world'
                        options={languageOptions}
                        search
                        text='Change Language'
                        onChange={this.handleLangChange}
                        value={lang}
                    />
                </Menu.Item>
            </Menu>
        )
    }
}